.footer {
  background-color: var(--container-color);
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 2rem;
}

.footer_container {
  padding: 2rem 0 6rem;
}

.footer_title,
.footer_link {
  color: var(--title-color);
}

.footer_title {
  text-align: center;
  margin-bottom: var(--mb-2);
}

.footer_link:hover {
  color: var(--title-color-dark);
}

.footer_list {
  display: flex;
  justify-content: center;
  column-gap: 1.5rem;
  margin-bottom: var(--mb-2);
}

.footer_social {
  display: flex;
  justify-content: center;
  column-gap: 1.25rem;
}

.footer_social-link {
  background-color: var(--title-color);
  color: var(--container-color);
  font-size: 1.125rem;
  padding: 0.3rem 0.5rem;
  border-radius: 0.5rem;
  display: inline-flex;
  transition: .3s;
}

.footer_social-link:hover {
  background-color: var(--title-color-dark);
  transform: scale(1.2);
}

.footer_copyright {
  display: block;
  margin-top: 4.5rem;
  color: var(--title-color);
  text-align: center;
  font-size: var(--small-font-size);
}